/***/ (function(module, exports) {

	module.exports = {"data":{"markdownRemark":{"html":"<h2 id=\"the-human-condition\"><a href=\"#the-human-condition\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>The Human Condition</h2>\n<p>We humans forget, are lazy, and lose more information than we retain. This is not helped by the attention economy, which inundates us with stuff. All kinds of stuff.</p>\n<p>As consumers, we must <em>pay</em> attention, attention being a scarce resource, to all sorts of different content - both online and off. The more information we consume, the more it seems we lose. Surely there is a way to keep more of this information, turning it into knowledge rather than waste.</p>\n<h2 id=\"enter-knowledge-management-systems\"><a href=\"#enter-knowledge-management-systems\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Enter Knowledge Management Systems</h2>\n<p>Drum roll...</p>\n<p><em>oohs and ahs.</em></p>\n<p>Knowledge management systems, or KMS for short, are what we humans use to store our stuff. We take it from the internet and save it, or we write it up and save it. We save stuff constantly, both work-related and non-work related.</p>\n<p>KMS should make it super easy for us to do what the acronym stands for, manage knowledge, or keep track of the information that we have written or pulled from the internet and saved.</p>\n<p>But KMS as we know it really stinks.</p>\n<p>When saving personal documents, either you are one of the 1% jedi humans who maintain perfect naming convention discipline and folder structure. Or, you are like the other 99% of us that and just hit <code>command + s</code> and forget about it.</p>\n<p>At surface level, it would seem that this is not that big a deal...just spend a couple minutes searching your PC or Mac and you will most likely find the folder, file, and information you are looking for (I bet Google would have been easier, though).</p>\n<blockquote>\n<p>What did I do with that file? Hmmm</p>\n</blockquote>\n<p>Sound familiar? This happens all too often and is really just such a bore! Now, think about the KMS for massive companies that have hundreds or thousands of employees.</p>\n<p>Over time, thousands of folders have given birth to thousands of sub-folders - all pregnant with documents in all shapes, sizes and formats.</p>\n<p>Who is to say what goes where? Dave, who is my favorite generic cube hound, may have a different idea of where one file should be saved than say Janet. For example, the latest prospectus on the digital advertising market is saved down by Dave. He has plenty of options.</p>\n<p>Maybe the \"adtech\" folder.</p>\n<p>Maybe the \"market_research\" folder.</p>\n<p>Dave might be a desktop kind of guy. The KMS is Dave's oyster.</p>\n<p>Dave also has a lot of different ideas on what to call the file.</p>\n<p>Propsectus-adtech-jan-2018</p>\n<p>OR</p>\n<p>Market-research-adtech-prospectus</p>\n<p>It's been ten minutes already and Dave has finally decided on a naming convention and folder. Go Dave!</p>\n<p>Both options make sense, but when searching a company database, the naming convention can make all the difference between watching the computer's wheels spin or finding your document when you need to.</p>\n<h2 id=\"knowledge-management-irl\"><a href=\"#knowledge-management-irl\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Knowledge Management IRL</h2>\n<p>Dave always makes me reflect on my own experiences. He is great at that.</p>\n<p>When I was an investment banking analyst, we had an absolutely massive amount of folders and files. It was my job to keep all these documents organized and retrieve the ones we needed whenever anyone asked.</p>\n<p>Yeah, fat chance.</p>\n<p>There were sometimes hundreds of versions of the same document in a folder. Other times files that belonged one place had been mistakenly dropped in a folder that seemed related, but really wasn't.</p>\n<p>This particular issue was especially bad on one project I was given because the previous analyst who worked on it completely threw out any inkling of organization when he found out he got an offer at a private equity shop. The next few months of working at the bank now did not matter to this young man, and neither did proper KM etiquette.</p>\n<p>The project I am referring to had died for a while, but randomly resuscitated (as far too many zombie projects do) and I had to scramble to find the files my team needed. It took me so long that they asked me to reorganize the entire behemoth that was the folder. It took me all night.</p>\n<p>I will never forgive you previous analyst!</p>\n<p>Files and folders do not work because there are inherent biases in any kind of shared curation. Imagine if instead of Google, it was Bank of America that supplied the search technology for consumers looking for retail banking products. That wouldn't work, would it?</p>\n<p>Obviously not. The same kind of biases happen in file saving and curation. It is unfortunately too much for humans to handle and actually creates serious issues that can severely hurt the internal operations of a firm.</p>\n<p><strong>We at Hex Ventures are working to help people pay more attention by spending less time.</strong></p>","timeToRead":3,"excerpt":"The Human Condition We humans forget, are lazy, and lose more information than we retain. This is not helped by the attention economy, whichâ€¦","frontmatter":{"title":"The Sad State of KMS","cover":"","date":"2018-02-01","category":"The KM Corner","tags":["Knowledge","Management","Files&Folders"]},"fields":{"slug":"/blog-hate-files-and-folders"}}},"pathContext":{"slug":"/blog-hate-files-and-folders"}}

/***/ })

